
.form-horizontal{
	width: 90%;
	margin: 20px auto;
}
h3{
	font-size: 2em;
}
.form-group {
  padding: 5px 10px 5px 10px;
}
.form-horizontal label {
	display: block;
	display: -webkit-box;
	display: -moz-box;
	display: box;	
	-moz-box-orient: horizontal;
	-moz-box-pack: end;
	-moz-box-align: center;
	-webkit-box-orient: horizontal;
	-webkit-box-pack: end;
	-webkit-box-align: center;
	box-orient: horizontal;
	box-pack: end;
	box-align: center;
	height: 40px;
	float: left;
	padding-right: 3%;
	width: 20%;
	min-width: 80px;
	font-size: 13px;
	color: #888;
	letter-spacing: 3px;
	text-transform: uppercase;
	text-align: right;
	text-shadow: 1px 1px 1px rgba(255,255,255,0.6);
}
.form-horizontal input{
	box-sizing: border-box; 
	border: 1px solid #ddd;
	padding: 10px;
	font-weight: bold;
	text-shadow: 0 1px 1px rgba(255,255,255,0.8);
	color: #666;
	width: 64%;
	background: #ffffff;
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#ffffff), color-stop(7%,#f7f7f7), color-stop(22%,#f7f7f7), color-stop(100%,#ffffff));
	background: -webkit-linear-gradient(top, #ffffff 0%,#f7f7f7 7%,#f7f7f7 22%,#ffffff 100%);
	background: linear-gradient(top, #ffffff 0%,#f7f7f7 7%,#f7f7f7 22%,#ffffff 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ffffff',GradientType=0 );
	box-shadow: 0px 1px 1px rgba(255,255,255,0.7), 1px 1px 2px rgba(0,0,0,0.1) inset;
}
.form-horizontal input:focus {
	background: #f9fcef;
	outline: none;
}

.btn-group{
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.btn-primary{
  display: inline-block;
  margin: 0.6rem;
  color:#fff;
  padding: 1rem;
  border:0;
  border-radius: 5px;
  background-color: #4CB0F9;
  /*background: #1B9AF7;*/
  font-size: 1em;
  -webkit-transition:  0.4s;
  transition:  0.4s; 
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .form-horizontal{
    width: 100%;
  }
  .form-horizontal h3{
    margin-left: 5%;
  }
  .form-horizontal label {
    width: 10%;
    min-width: 60px;
  }
  .form-horizontal input{
    width: 73%;
  }
}



