
.banner{
  width: 100%;
  height: 600px;
  background:url(../../static/dist/img/hero.jpg) no-repeat 0 0;
  background-size: cover;
   /*-webkit-filter: blur(5px);
     -moz-filter: blur(5px);
      -ms-filter: blur(5px);   
          filter: blur(5px);   */
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-align-items: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column ;
          flex-direction: column ;
  font-family: "STKaiti";
  color: #fff;
}
.banner h1{
  font-size: 2.5em;
}
.banner p{
  opacity: .5;
  -moz-opacity: .5;
  filter:alpha(opacity=50);
  font-size: 1.2em;
}

@media screen and (max-width: 500px) {   
  .banner h1{
    font-size: 1.2rem;
    position: absolute;
    top: 330px;
  }
  .banner p{
    font-size: 1rem;
    position: absolute;
    top: 350px;
  }
  .banner{
    height: 700px;
  }
}



