

html {
  height: 100%;
}

header {
  width: 100%;
  height: 80px;
}

a{
  text-decoration: none;
}

nav {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-align-items: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

nav .logo {
  width: 3.125rem;
  height: 3.125rem;
  margin-right: 20px;
  border-radius: 100%;
  -webkit-filter: invert(90%);
  filter: invert(90%);
  margin-left: 12%;
  cursor: pointer;
  -webkit-transition:  0.4s;
  transition:  0.4s; 
}

.nav-list {
  height: 70px;
  padding: 0 20px;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-align-items: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #000;
  font-size: 20.8px;
  font-weight: 900;
  cursor: pointer;
  border-top: 5px solid #fff;
  border-bottom: 5px solid #fff;
  -webkit-transition:  0.4s;
  transition:  0.4s; 
}

.nav-list:hover { 
  border-bottom: 5px solid #80A8CC;
}

nav .v-link-active {
  color: #80A8CC;
}

@media screen and (max-width: 500px) {
  nav .logo{
    margin-left: 3%;
    width: 10%;
    height: auto;
  }
  .nav-list {
  }
}
@media screen and (max-width: 400px) {
  .nav-list {
    padding:0 0.2rem;
  }
}




