
.todoForm{
  margin-top: 20px;
  text-align: center;
}
.formgroup{
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.todoInput{
  padding:1em;
  border: 2px solid #555;
  width: 24em;
  border-radius: 3px;
  font-size: 1.1em;
  opacity: .8;
  -webkit-transition:  0.4s;
  transition:  0.4s; 
}
.todoBtn{
  margin: 10px;
  color:#fff;
  padding: 1.28em;
  padding-top: 1.2em;
  border:0;
  width: 6em;
  border-radius: 5px;
  background-color: #4CB0F9;
  /*background: #1B9AF7;*/
  font-size: 1em;
  -webkit-transition:  0.4s;
  transition:  0.4s; 
  cursor: pointer;
}
.todoBtn:hover, .todoBtn:focus, .todoBtn:active, .todoBtn.active, .todoBtn.is-active{
  background-color: #1B9AF7;
  border-color: #1B9AF7;
  color: #FFF; 
  cursor: pointer;
}

#todoUl{
  -webkit-transition:  0.4s;
  transition:  0.4s; 
}

.todoItems{
  width: 70%;
  margin: 10px auto;
  position: relative;
  white-space:normal;
  background-color: #fff;
  -webkit-transition:  0.2s;
  transition:  0.2s; 
  overflow: hidden;
  /*height: 0;*/
}

.todoItems p{
  padding:20px;
  line-height: 2em;
  word-wrap: break-word;
  text-align: left;
}

.todoItems .delete{
  position: absolute;
  right: 5px;
  top: 5px;
  width: 1.1em;
  height: 1.1em;
  border-radius: 5px;
  padding: 5px;
  background-color: #fff;
  -webkit-transition:  0.4s;
  transition:  0.4s;
}
.todoItems .delete:hover{
  cursor: pointer;
  color: #fff;
  background-color: #F45551;
}
@media screen and (max-width: 620px) {  
  .formgroup{
    display: block;
  } 
  .todoInput{
    width: 100%;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box;
  }
  .todoBtn{
    margin:10px 0;
    width: 100%;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box;
  }
  .todoItems{
    width: 100%;
  }
}




